<template>
  <div class="components-container">
    <code>
      <a class="link-type" href="https://github.com/rowanwins/vue-dropzone"> dropzone </a> ,
        
    </code>
    <div class="editor-container">
      <dropzone v-on:dropzone-removedFile="dropzoneR" v-on:dropzone-success="dropzoneS" id="myVueDropzone" url="https://httpbin.org/post"></dropzone>
    </div>
  </div>
</template>

<script>
import Dropzone from '@/components/Dropzone'

export default {
  name: 'dropzone-demo',
  components: { Dropzone },
  methods: {
    dropzoneS(file) {
      console.log(file)
      this.$message({ message: '', type: 'success' })
    },
    dropzoneR(file) {
      console.log(file)
      this.$message({ message: '', type: 'success' })
    }
  }
}
</script>





